import {Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogComponent} from './dialog/dialog.component';
import {Sheet533Service} from './sheet533.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {ConfirmationDialogComponent} from './confirmation-dialog/confirmation-dialog.component';
import { ReportService } from 'src/app/pages/generate-report/report.service';
import { ReportData } from 'src/app/data/report-data';
import { Location } from '@angular/common';

// @ts-ignore
@Component({
  selector: 'app-sheet533',
  templateUrl: './sheet533.component.html',
  styleUrls: ['./sheet533.component.css']
})
export class Sheet533Component implements OnInit {

  dataSource: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'code', 'description', 'gross_amount',
    'additions', 'accumulated_depreciation', 'impairment', 'carrying_amount'];
    confirmDelete: boolean;
    reportData: ReportData;
    reportSelectedDate: string;
  
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
  
    constructor(private dialog: MatDialog,
      private sheet533Service: Sheet533Service,
      private reportService: ReportService,
      protected readonly location: Location) { }
  
    ngOnInit(): void {
      this.getAllMDFIR533Data();
       this.reportService.getTableData().forEach((e) => {
        if (e.return_code === 'MDFIR533'){
         this.reportData = e;
        }  
      });
  
      this.reportService.selectedDate.subscribe((date) => {
        this.reportSelectedDate = date;
      });
    }

  // tslint:disable-next-line:typedef

  openDialog(): void {
    this.dialog.open(DialogComponent, {
       width: '30%',
    }).afterClosed().subscribe(value => {
      if (value === 'save'){this.getAllMDFIR533Data();
      }
    });
  }

  goBack() {
    this.location.back();
  }

  getAllMDFIR533Data(): void {
    this.sheet533Service.getMDFIR533Data().subscribe({
      next: (res) => {
        console.log(res.sheet533);
        this.dataSource = new MatTableDataSource(res.sheet533);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, error: () => {console.log('Error while fetching data');
      }
    });
  }

  // tslint:disable-next-line:typedef
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  editReportRow(row: any): void {
    this.dialog.open(DialogComponent, {width: '30%', data: row}).afterClosed().subscribe(value => {
      if (value === 'update'){
        this.getAllMDFIR533Data();
      }
    });
  }

  deleteReportRow(id: any): void {
    this.sheet533Service.deleteMDFIR533Data(id).subscribe({
      next: value => {
        alert('Deleted sucessfully');
        this.getAllMDFIR533Data();
      }, error: err => {
        alert('Failed');
      }
    });
  }

  // tslint:disable-next-line:typedef
  openConfirmationDialog(id) {

    this.dialog.open(ConfirmationDialogComponent, {
      width: '17%',
      data: true
    }).afterClosed().subscribe(value => {
      if (value === 'delete'){
        this.deleteReportRow(id);
        this.getAllMDFIR533Data();
      }
    });
  }
}
